import { DashboardService } from './../providers/api/dashboard.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { VeiculoService } from './../providers/api/veiculo.service';
import { AuthHttpAuthenticationInterceptor } from './../providers/interceptor/auth-authentication-http-interceptor';
import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS, HttpXhrBackend, HttpBackend } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { Items } from '../mocks/providers/items';
import { Settings, User, Api } from '../providers';
import { MyApp } from './app.component';
import { SearchService } from '../providers/api/search.service';
import { GoogleMaps } from '@ionic-native/google-maps';
import { DatePicker } from '@ionic-native/date-picker';
import { CalendarModule } from "ion2-calendar";

import { OpenNativeSettings } from '@ionic-native/open-native-settings';
import { NativeGeocoder } from '@ionic-native/native-geocoder';
import { LocationAccuracy } from '@ionic-native/location-accuracy';

import { AndroidPermissions } from '@ionic-native/android-permissions';
import { Camera } from '@ionic-native/camera';
import { Diagnostic } from '@ionic-native/diagnostic';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { Base64 } from '@ionic-native/base64';
import { File } from '@ionic-native/file';
import { FileOpener } from '@ionic-native/file-opener';
// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any time. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new Settings(storage, {
    option1: true,
    option2: 'Ionitron J. Framework',
    option3: '3',
    option4: 'Hello'
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot(),
    CalendarModule
  ],
  exports: [
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Items,
    User,
    VeiculoService,
    DashboardService,
    SearchService,
    SplashScreen,
    GoogleMaps,
    OpenNativeSettings,
    FileOpener,
    Camera,
    LocalNotifications,
    Base64,
    File,
    Diagnostic,
    AndroidPermissions,
    NativeGeocoder,
    LocationAccuracy,
    DatePicker,
    StatusBar,
    {
      provide: HTTP_INTERCEPTORS, useClass: AuthHttpAuthenticationInterceptor, multi: true
    },
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    HttpXhrBackend,
    { provide: HttpBackend, useExisting: HttpXhrBackend }
  ]
})
export class AppModule { }



