import { DataStore } from './data-store';

import { Marker } from '@ionic-native/google-maps';

export class Marcador {
    veiculoId: number;
    marker: Marker = null;
    veiculo:DataStore;

    constructor(veiculoId: number, veiculo:DataStore) {
        this.veiculoId = veiculoId;
        this.veiculo = veiculo;
    }
}



